.loadMain {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;

  >div {
    // width: 100%;
    // height: 100vh;

    >img {
      width: 35px;
      height: 100px;
      
      border-radius: 50%;
      object-fit: contain;
    }
  }

  .loadTwo {
    display: flex;
    flex-direction: column;
  }

  .imgLoadOne {
    animation: loadleftright 0.5s cubic-bezier(0.5, 0.05, 0.5, 0.5) alternate infinite;
  }

  .imgLoadTwo {
    animation: loadupdown 0.5s cubic-bezier(0.5, 0.05, 0.5, 0.5) alternate infinite;

  }

  .imgLoadThree {
    animation: loaddownup 0.5s cubic-bezier(0.5, 0.05, 0.5, 0.5) alternate infinite;

  }

  .imgLoadFour {
    animation: loadrightleft 0.5s cubic-bezier(0.5, 0.05, 0.5, 0.5) alternate infinite;
    
  }
}

.disableBtn {
  color: $color6 !important;
  background-color: rgb(191, 191, 191) !important;
  cursor: not-allowed !important;
}

#customMessage {
  font: 900 2rem $text2;
  width: 100%;
  height: 100vh;
  display: grid;
  place-items: center;
}